.skillsBlock {
    min-height: 100vh;
    background-color: #3ba13b;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #646363;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -5px
}

@media screen and (max-width: 625px) {
    .skills {
        justify-content: center;
        align-items: center;
    }
}