.project {
    width: 400px;
    background-color: #6b6767;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 5px 5px;
}

.projectImageContainer {
    background-color: gold;
    width: 100%;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.projectImageContainer a {
    background: #479fd2;
    border: 1px solid;
    height: 25px;
    width: 90px;
    text-decoration: none;
}
