.projectsBlock {
    min-height: 100vh;
    background-color: #ffd4f0;
}

.container {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #646363;
}

.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 1024px) {
    .projects {
        justify-content: center;
        align-items: center;
    }
}
